*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: $fz-default;
  line-height: 24px;
  font-family: $ff-placeholder;
  color: $color-default-black;

  scroll-behavior: smooth;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // Ð¾Ð¿Ñ‚Ð¸Ð¼Ð¸Ð·Ð°Ñ†Ð¸Ñ Ð²Ñ‹Ñ€Ð°Ð²Ð½Ð¸Ð²Ð°Ð½Ð¸Ñ ÑˆÑ€Ð¸Ñ„Ñ‚Ð° Ð¾Ñ‚Ð½Ð¾ÑÐ¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ñ†ÐµÐ½Ñ‚Ñ€Ð° ÑÑ‚Ñ€Ð¾ÐºÐ¸
  text-rendering: optimizeLegibility;
  // ÐµÑÐ»Ð¸ Ð¿Ð¾ Ð¿Ñ€ÐµÐ¶Ð½ÐµÐ¼Ñƒ ÐµÑÑ‚ÑŒ Ð¿Ñ€Ð¾Ð±Ð»ÐµÐ¼Ñ‹ Ñ Ð²Ñ‹Ñ€Ð°Ð²Ð½Ð¸Ð²Ð°Ð½Ð¸ÐµÐ¼
  // https://transfonter.org/ - Ð²ÐºÐ»ÑŽÑ‡Ð¸Ñ‚Ðµ Ð½Ð°ÑÑ‚Ñ€Ð¾Ð¹ÐºÑƒ https://prnt.sc/12rnt6g Ð¸ Ð¿ÐµÑ€ÐµÐºÐ¾Ð½Ð²ÐµÑ€Ñ‚Ð¸Ñ€ÑƒÐ¹Ñ‚Ðµ ÑˆÑ€Ð¸Ñ„Ñ‚
}

body {
  width: 100%;
  height: 100%;

  font-family: $ff-PT-Mono;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: $color-black-pearl-dark;

  background-color: $color-alice-blue;
}

a {
  color: $color-default-black;
  text-decoration: none;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

// chrome autofill background removal
// ÐµÑÐ»Ð¸ Ð½Ð° Ð¿Ñ€Ð¾ÐµÐºÑ‚Ðµ Ñƒ Ð¸Ð½Ð¿ÑƒÑ‚Ð¾Ð² Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÑŽÑ‚ÑÑ Ñ€Ð°Ð·Ð½Ñ‹Ðµ Ñ†Ð²ÐµÑ‚Ð° Ñ„Ð¾Ð½Ð¾Ð²\Ñ‚ÐµÐºÑÑ‚Ð° -
// ÑƒÐ´Ð°Ð»Ð¸Ñ‚Ðµ ÑÑ‚Ð¾ Ð¸Ð· Ð³Ð»Ð¾Ð±Ð°Ð»Ð° Ð¸ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐ¹Ñ‚Ðµ Ð»Ð¾ÐºÐ°Ð»ÑŒÐ½Ð¾ Ñ Ð½ÑƒÐ¶Ð½Ñ‹Ð¼Ð¸ Ñ†Ð²ÐµÑ‚Ð°Ð¼Ð¸
// rgba Ð½Ðµ Ð¿Ð¾Ð´Ð¾Ð¹Ð´ÐµÑ‚, ÑÐºÐ¾Ð½Ð²ÐµÑ€Ñ‚Ð¸Ñ€ÑƒÐ¹Ñ‚Ðµ Ñ†Ð²ÐµÑ‚ Ð² hex Ð±ÐµÐ· Ð¿Ñ€Ð¾Ð·Ñ€Ð°Ñ‡Ð½Ð¾ÑÑ‚Ð¸
// ÐµÑÐ»Ð¸ Ð² ÑÑ‚Ð¸Ð»ÑÑ… ÑƒÐ¶Ðµ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐµÑ‚ÑÑ box-shadow ÐµÑÑ‚ÑŒ Ð´Ñ€ÑƒÐ³Ð¾Ðµ Ñ€ÐµÑˆÐµÐ½Ð¸Ðµ -
// Ð·Ð°Ð´Ð°Ñ‚ÑŒ Ðº ÑÐ¿Ð¸ÑÐºÑƒ Ñ‚Ñ€Ð°Ð½Ð·Ð¸ÑˆÐµÐ½Ð¾Ð² `background-color 10000000s ease-out`
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px $color-default-white;

  -webkit-text-fill-color: $color-default-black;
}

// firefox placeholder \ invalid fix + ios bdrs
input,
textarea {
  border-radius: 0;

  &::placeholder {
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

select {
  border-radius: 0;
}

// ie11 X removal
input {
  &::-ms-clear,
  &::-ms-reveal {
    display: none;
  }
}

// chrome search X removal
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none;
}

// input[number] arrows removal
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;

  appearance: none;
}

input[type="number"] {
  appearance: textfield;
}

// ios button \ inputs reset
select,
textarea,
input:matches([type="email"],
  [type="number"],
  [type="password"],
  [type="search"],
  [type="tel"],
  [type="text"],
  [type="url"]) {
  appearance: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none;
}
